<template>
  <Container
    style="padding-top: 100rpx;"
    v-model="current"
    tabbar
    :is-back="false"
    :style="[{backgroundColor:'var(--background-color-1)'}]"
  >
    <view slot="action">
      <picker
        @change="PickerChange"
        :value="index"
        :range="picker"
      >
        <view class="picker">
          <text
            class="text"
            style="padding-right:16rpx"
          >
            {{index>-1?picker[index]:'选择客户'}}
          </text>
          <Arrow
            style-inner="border-color:#fff;border-width: 4rpx 4rpx 0 0;"
            :size="20"
          />
        </view>
      </picker>
    </view>
    <template v-if="current==='home'">
      <view
        class="cu-bar search fixed"
        :style="[{top:CustomBar + 'px',backgroundColor:'var(--background-color-0)'}]"
      >
        <view class="search-form round">
          <text
            class="cuIcon-search"
            style="color:var(--font-color-1)"
          ></text>
          <input
            type="text"
            placeholder="请输入药材名称/拼音缩写"
            confirm-type="search"
            style="background-color:transparent;color:var(--font-color-0)"
          />
        </view>
        <view class="action">
          <button
            class="cu-btn shadow-blur round"
            style="background-color:var(--background-color-main-0);color:#fff;"
          >搜索</button>
        </view>
      </view>
      <scroll-view
        scroll-y
        class="indexes"
        :scroll-into-view="'indexes-'+ listCurID"
        :style="[{height:'calc(100vh - '+ CustomBar + 'px - 50px)',backgroundColor:'var(--background-color-1)'}]"
        :scroll-with-animation="true"
        :enable-back-to-top="true"
      >
        <block
          v-for="(item,index) in list"
          :key="index"
        >
          <view
            :class="'indexItem-' + item.name"
            :id="'indexes-' + item.name"
            :data-index="item.name"
          >
            <view class="padding">{{item.name}}</view>
            <view class="cu-list menu-avatar no-padding">
              <view
                class="cu-item"
                v-for="(items,sub) in 2"
                :key="sub"
              >
                <view class="cu-avatar round lg">甘</view>
                <view class="content">
                  <view class="content-left"><text>甘草</text>
                    <text>0.5g/3g</text>
                    <text>￥5.12</text>
                  </view>
                  <image src="data:image/png;base64," />
                </view>
              </view>
            </view>
          </view>
        </block>
      </scroll-view>
      <view
        class="index-bar"
        :style="[{height:'calc(100vh - ' + CustomBar + 'px - 50px)'}]"
      >
        <view
          class="index-bar-box"
          @touchstart="tStart"
          @touchend="tEnd"
          @touchmove.stop="tMove"
        >
          <view
            class="index-bar-item"
            v-for="(item,index) in list"
            :key="index"
            :id="index"
            @touchstart="getCur"
            @touchend="setCur"
          > {{item.name}}</view>
        </view>
      </view>
      <!--选择显示-->
      <view
        v-show="!hidden"
        class="index-toast"
      >
        {{listCur}}
      </view>
    </template>
    <Mine v-else-if="current==='mine'" />
    <ShoppingCart v-else-if="current==='shopping-cart'" />
    <Track v-else-if="current==='track'" />
  </Container>
</template>

<script>
import Arrow from '@/components/arrow';
import Mine from '../mine';
import Track from '../track';
import ShoppingCart from '../shopping-cart';
export default {
  components: {
    Arrow,
    Mine,
    ShoppingCart,
    Track,
  },
  onPullDownRefresh() {
    uni.stopPullDownRefresh();
  },
  data() {
    return {
      StatusBar: this.StatusBar,
      CustomBar: this.CustomBar,
      current: 'home',
      hidden: true,
      listCurID: '',
      list: [],
      listCur: '',
      index: 0,
      picker: [
        '千金大药房 (保利麓谷林语店)',
        '千金大药房 (梅溪湖店)',
        '千金大药房 (尖山湖店)',
      ],
    };
  },
  onLoad() {
    let list = [{}];
    for (let i = 0; i < 26; i++) {
      list[i] = {};
      list[i].name = String.fromCharCode(65 + i);
    }
    this.list = list;
    this.listCur = list[0];
  },
  onReady() {
    let that = this;
    uni
      .createSelectorQuery()
      .select('.index-bar-box')
      .boundingClientRect(function (res) {
        that.boxTop = res.top;
      })
      .exec();
    uni
      .createSelectorQuery()
      .select('.indexes')
      .boundingClientRect(function (res) {
        that.barTop = res.top;
      })
      .exec();
  },
  methods: {
    PickerChange(e) {
      this.index = e.detail.value;
    },
    //获取文字信息
    getCur(e) {
      this.hidden = false;
      this.listCur = this.list[e.target.id].name;
    },
    setCur(e) {
      this.hidden = true;
      this.listCur = this.listCur;
    },
    //滑动选择Item
    tMove(e) {
      let y = e.touches[0].clientY,
        offsettop = this.boxTop,
        that = this;
      //判断选择区域,只有在选择区才会生效
      if (y > offsettop) {
        let num = parseInt((y - offsettop) / 20);
        this.listCur = that.list[num].name;
      }
    },

    //触发全部开始选择
    tStart() {
      this.hidden = false;
    },

    //触发结束选择
    tEnd() {
      this.hidden = true;
      this.listCurID = this.listCur;
    },
    indexSelect(e) {
      let that = this;
      let barHeight = this.barHeight;
      let list = this.list;
      let scrollY = Math.ceil((list.length * e.detail.y) / barHeight);
      for (let i = 0; i < list.length; i++) {
        if (scrollY < i + 1) {
          that.listCur = list[i].name;
          that.movableY = i * 20;
          return false;
        }
      }
    },
  },
};
</script>

<style lang="scss">
.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  image {
    width: 40rpx;
    height: 40rpx;
  }
}
.content-left {
  flex: 1;
  text {
    flex: 1;
  }
}
.picker {
  font-size: 36rpx;
  padding: 0 28rpx;
  display: flex;
  flex-direction: row;
  align-items: center;
  .text {
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
  }
}
.indexes {
  position: relative;
  padding-bottom: calc(env(safe-area-inset-bottom) / 2 + 100rpx);
}

.index-bar {
  position: fixed;
  right: 0px;
  bottom: 0px;
  padding: 20upx 20upx 20upx 60upx;
  display: flex;
  align-items: center;
}

.index-bar .index-bar-box {
  width: 40upx;
  height: auto;
  background: var(--background-color-0);
  display: flex;
  flex-direction: column;
  box-shadow: var(--box-shadow-0);
  border-radius: 10upx;
}

.index-bar-item {
  flex: 1;
  width: 40upx;
  height: 40upx;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24upx;
}

movable-view.index-bar-item {
  width: 40upx;
  height: 40upx;
  z-index: 9;
  position: relative;
}

movable-view.index-bar-item::before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 10upx;
  height: 20upx;
  width: 4upx;
  background-color: #f37b1d;
}

.index-toast {
  position: fixed;
  top: 0;
  right: 80upx;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100upx;
  height: 100upx;
  border-radius: 10upx;
  margin: auto;
  color: #fff;
  line-height: 100upx;
  text-align: center;
  font-size: 48upx;
}
</style>
